<template>
  <div>
    <div class="footer">
      <div class="footer__item" v-for="(item, index) in items" :key="index" :class="{ active: isActive(item.value) }"
        @click="setActive(item.value)">
        <font-awesome-icon :icon="item.icon" />
        <p>{{ item.name }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeItem: 0,
      items: [
        {
          icon: "fa-solid fa-house",
          name: "Home",
          value: 0
        },
        {
          icon: "fa-solid fa-bag-shopping",
          name: "Cart",
          value: 1
        },
        {
          icon: "fa-solid fa-cart-shopping",
          name: "Orders",
          value: 2
        },
        {
          icon: "fa-solid fa-wallet",
          name: "Wallet",
          value: 3
        },
        {
          icon: "fa-solid fa-user",
          name: "Profile",
          value: 4
        }
      ]
    }
  },
  methods: {
    isActive: function (menuItem) {
      return this.activeItem === menuItem;
    },
    setActive: function (menuItem) {
      this.activeItem = menuItem;
    },
  }
}
</script>

<style lang="scss" scoped>
.footer {
  position: fixed;
  height: 70px;
  background-color: #fff;
  bottom: 0px;
  left: 0px;
  right: 0px;
  margin-bottom: 0px;
  display: flex;
  padding-left: 5%;
  padding-right: 5%;
  align-items: center;
  justify-content: space-around;
  border-top: 0.5px solid #e5e5e5de;

  &__item {
    text-align: center;

    svg {
      height: 24px;
      color: #ccc;
    }

    p {
      padding-top: 5px;
      font-size: 12px;
      color: #ccc;
    }
  }

  .active {

    svg,
    p {
      color: #000;
    }
  }
}
</style>